
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
export default {
data(){
return {
resObj: {}
}
},
mounted(){
this.$vux.loading.show({
text: '加载中...'
})
this.initInfo();
},
methods:{
getString(info){
return typeof info !='undefined' ? info : ''
},
initInfo(){

this.$ajax.post('',{
'MSH.1':'healthFileBasicInfoService',
'MSH.2':'getById',
'CHZBM':this.$root.loginInfo.CHZBM,
'GRDAID':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
this.resObj = data;
}
}).catch(error => {
console.log(error);
});

}

},
components:{
XHeader,
Collapse
}
}



